<template>
  <footer id="footer" class="section bg-dark text-white">
    <div class="container">
      <div class="row">
        <div
          class="col-lg-6 text-center text-lg-start wow fadeInUp"
          style="visibility: visible"
        >
          <p class="mb-2 mb-lg-0">
            Copyright © {{ currentDate() }}
            <a class="fw-600" href="#">Kaushik</a>. All Rights Reserved.
          </p>
        </div>
        <div class="col-lg-6 wow fadeInUp" style="visibility: visible">
          <p class="mb-0 text-center text-lg-end">
            Designed by <a class="fw-600" href="#home">Kaushik Nakrani</a>
          </p>
        </div>
      </div>
    </div>
  </footer>
  <div
    class="modal fade"
    id="modalpwd"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">Confirmation</h5>
          <button
            type="button"
            class="btn-close loginclose"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="row modal-body">
          <div class="col-md-12">
            <p class="text-danger">{{ msg }}</p>
            <form @submit.prevent="userpswd">
              <div class="form-group">
                <label for="password">Password</label>
                <input
                  type="password"
                  class="form-control"
                  id="password"
                  placeholder="Password"
                  v-model="password"
                  required
                />
              </div>
              <button type="submit" class="btn knbtn mt-3">Submit</button>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CryptoJS from 'crypto-js'

export default {
  name: "footer-bottom",
  data(){
    return{
      password:"",
      msg:"",
    }
  },
  methods: {
    currentDate() {
      const current = new Date();
      const date = `${current.getFullYear()}`;
      return date;
    },
    userpswd() {

      if(localStorage.getItem('bdkey') === null){
      

        let convertdata = "kaushik333";

        if(convertdata == this.password){
          let cryptoconvrt = CryptoJS.AES.encrypt(convertdata,'nakrani').toString();
          localStorage.setItem('bdkey', cryptoconvrt);
          this.$router.push('portfolio');
          document.querySelector('.loginclose').click();
        }else{
          this.msg = "Please enter valid password"
        }
      }
    },
  },
};
</script>

<style></style>
