<template>
  <div class="modal fade modal-xl" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel"
    aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">{{ title }}</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="row modal-body">
          <div class="col-md-12">
            <div id="carouselExampleCaptions" class="carousel carousel-dark" data-bs-ride="carousel">
              <div class="carousel-inner">
                <div v-for="(pimg, index) in project_img" :key="pimg"
                  :class="index == 0 ? 'active carousel-item' : 'carousel-item'">
                  <div class="carousel-caption d-md-block px-5" style="position: relative; left: 0%;bottom: 0px;">
                    <img :src="require(`@/${pimg}`)" class="img-fluid d-inline-block w-100 shadow" />
                  </div>
                </div>
              </div>
              <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleCaptions"
                data-bs-slide="prev">
                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                <span class="visually-hidden">Previous</span>
              </button>
              <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleCaptions"
                data-bs-slide="next">
                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                <span class="visually-hidden">Next</span>
              </button>
            </div>
          </div>

          <div class="col-md-12">
            <h4 class="text-4 fw-600" v-if="pdescription">Project Info:</h4>
            <p v-html="pdescription"></p>
            <ul class="list-style-2">
              <li>
                <span class="text-dark fw-600 me-2">Date:</span>{{ prdate }}
              </li>
              <li  v-if=purl>
                <span class="text-dark fw-600 me-2">URL:</span><a
                  class="btn btn-primary shadow-none rounded-0 px-3 py-1" :href="purl" target="_blank">Click Here! <i
                    class="fas fa-external-link-alt ms-1"></i></a>
              </li>
            </ul>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "model_popup",
  props: {
    title: String,
    project_img: Array,
    pdescription: String,
    prdate: Date,
    purl:URL
  },
};
</script>

<style>
.carousel .item>img {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 100%;
  height: 100%;
}
</style>