import { createRouter, createWebHistory } from "vue-router";
import HomePage from "@/views/HomePage";
import CryptoJS from "crypto-js";

const routes = [
  {
    path: "/",
    name: "Home",
    component: HomePage,
  },
  {
    path: "/portfolio",
    name: "Portfolio",
    meta: {
      requiresAuth: true,
    },
    children: [
      {
        path: "",
        component: () => import("@/views/portfolio/Portfoliomore"),
      },
      {
        path: "wptheme",
        component: () => import("@/views/portfolio/wptheme"),
      },
      {
        path: "wpplugin",
        component: () => import("@/views/portfolio/wpplugin"),
      },
      {
        path: "wpshopping",
        component: () => import("@/views/portfolio/wpshopping"),
      },
      {
        path: "rectjs",
        component: () => import("@/views/portfolio/rectjs"),
      },
      {
        path: "vuejs",
        component: () => import("@/views/portfolio/vuejs"),
      },
      {
        path: "website",
        component: () => import("@/views/portfolio/website"),
      }
    ],
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  mode: "hash",
  routes: routes,
});

router.beforeEach((to, from, next) => {
  if (to.matched.some((record) => record.meta.requiresAuth)) {
    let convertdata = "kaushik333";
    const bdkeylocal = localStorage.getItem("bdkey");

    if (bdkeylocal !== null) {
      let getdecrypt = CryptoJS.AES.decrypt(bdkeylocal, "nakrani").toString(
        CryptoJS.enc.Utf8
      );

      if (convertdata !== getdecrypt) {
        localStorage.removeItem("bdkey");
        next({ name: "Portfolio" });
      } else if (getdecrypt === convertdata) {
        next();
      } else {
        next({ name: "Home" });
      }
    } else {
      next({ name: "Home" });
    }
  } else {
    next();
  }
});
export default router;
