<template>
  <section id="portfolio" class="section bg-light-1">
    <div class="container">
      <!-- Heading -->
      <p class="text-center mb-2" data-aos="fade-up" data-aos-once="true">
        <span class="knbtn px-2">Portfolio</span>
      </p>
      <h2 class="text-10 fw-600 text-center mb-5" data-aos="fade-up" data-aos-once="true">
        Some of my most recent projects
      </h2>
      <!-- Heading end-->

      <div class="portfolio" data-aos="fade-up" data-aos-once="true">
        <div class="row g-4 mt-3 portfolio-filter">
          <template v-for="(appdata, main_index) in fillter_data" :key="appdata.id">
            <div v-for=" (subdata, index) in appdata" :key="subdata" class="col-sm-6 col-lg-4 filter'"
              @click="project_data(index, main_index)" data-bs-toggle="modal" data-bs-target="#exampleModal">
              <div class="portfolio-box">
                <div class="portfolio-img">
                  <img class="img-fluid d-block" :src="require(`@/${subdata.projecr_image[0]}`)" alt="" />
                  <div class="portfolio-overlay">

                    <div class="portfolio-overlay-details">
                      <p class="text-primary text-8">
                        <i class="fas fa-file-alt"></i>
                      </p>
                      <h5 class="text-white text-5">{{ subdata.project_title }}</h5>
                      <span class="text-light">{{ subdata.term_name }}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </template>
        </div>
      </div>
    </div>
  </section>

  <ModelPopup :title="title" :project_img="project_img" :pdescription="project_desc" :prdate="pdate"
    :ptech="ptechnologies" :purl="psite" />
</template>

<script>
import jQuery from "jquery";
import logos_data from "@/json/bhavindata.json";
import ModelPopup from "@/components/ModelPopup.vue";

export default {
  name: "MyPortfolio",
  components: {
    ModelPopup
  },
  data() {
    return {
      app_image: logos_data,
      title: '',
      project_img: "",
      project_desc: '',
      pdate: '',
      ptechnologies: '',
      psite: '',
      fillter_data: ''
    }
  },
  methods: {
    filterbutton(e) {
      let allfilter = e.target.attributes["data-filter"].value;
      if (allfilter == "all") {
        jQuery(".filter").show("1000");
      } else {
        jQuery(".filter")
          .not("." + allfilter)
          .hide("3000");
        jQuery(".filter")
          .filter("." + allfilter)
          .show("3000");
      }

      jQuery(document).on("click", ".portfolio-menu a", function () {
        jQuery(".portfolio-menu").find("a").removeClass("active");
        jQuery(this).addClass("active");
      });
    },
    project_data(dataid, postion) {
      let navitem = this.app_image[postion][dataid][0]
      this.title = navitem.project_title;
      this.project_img = navitem.projecr_image;
      this.project_desc = navitem.project_desc;
      this.pdate = navitem.project_date;
      this.ptechnologies = navitem.project_technologies;
      this.psite = navitem.project_url;
    }
  },
  mounted() {
    const array = [];
    array.push({ "wptheme": this.app_image[0].wptheme[0] },{ "wpplugin": this.app_image[1].wpplugin[0] },{ "wpshopping": this.app_image[2].wpshopping[0] },{ "rectjs": this.app_image[3].rectjs[0] },{ "website": this.app_image[5].website[0] });
    this.fillter_data = array; 

  }
};
</script>

<style scoped>

</style>